.confirm {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  -webkit-border-radius: 0 0 5px 5px;
  -moz-border-radius: 0 0 5px 5px;
  border-radius: 0 0 5px 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &__question {
    padding: .62em 0; }

  &__buttons {
    margin-top: 5px;
    display: flex;
    justify-content: space-around;
    .button {
      padding: 0.87em 4.2em;
      &__red {
        margin-right: 10px;
        border: 1px solid #6a9fb1;
        background-color: transparent; }
      &__white {
        margin-left: 10px;
        background-color: #B80635;
        border: none; } } } }
